.food-title{
    position: absolute;
    bottom: 20px;
    left: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-family: 'Staatliches',sans-serif;
    font-size: 2em;
    color: #fff;
    border-radius: 30px;
    padding: 20px 10px;
    z-index: 9;
    background-color: #ffb84d63;
    backdrop-filter: blur(3px);
    -webkit-backdrop-filter: blur(3px);
}
