    .card-wrapper{
        width: 180px;
        height: 250px;
    }
}
.recipe-container{
    position: relative;
    width: 100vw;
    height: 100vh;
    display: grid;
    overflow: hidden;
}
/* RECIPES NAV BAR */
.recipes-nav-container{
    width: 10vw;
    height: 100vh;
    grid-column: 1;
    grid-row: 1 / span 3;
    padding: 30px 0px 30px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
}
/* RECIPES IMAGE */
.recipes-img-container{
    position: relative;
    width: 45vw;
    height: 100vh;
    grid-column: 2;
    grid-row: 1/ span 3;
}
.recipes-wrapper{
    position: absolute;
    width: 80%;
    height: 90%;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    object-fit: cover;
    border-radius: 50px;
    overflow: hidden;
}
.recipes-img{
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.watch-tutorial-btn-wrapper{
    position: absolute;
    bottom: 10%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
/* INGREDIENT */
.recipes-title-wrapper{
    width: 45vw;
    height: 10vh;
    grid-column: 3;
    grid-row: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}
.recipes-title{
    font-family: 'Staatliches',sans-serif;
    font-size: 1.8em;
}
/* RATING */
.rating-wrapper{
    width: 45vw;
    height: 10vh;
    grid-column: 3;
    grid-row: 2;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
/* INSTRUCTION */
.instructions-wrapper{
    width: 45vw;
    height: 80vh;
    grid-column: 3;
    grid-row: 3;
}
.insturctions{
    padding-left: 10px;
    font-size: 1.2em;
    text-align: left;
}
/* MOBILE TOP NAV */
.mobile-top-nav-wrapper{
    display: none;
    grid-row: 1;
    grid-column: 1;
    width: 100vw;
    height: 5vh;
}
.mobile-top-nav{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 20px 0px 20px;
    list-style: none;
}
/* background image */
.recipes-bg{
    position: absolute;
    right: -20%;
    bottom: -30%;
    width: 60vw;
    height: 90vh;
}
.recipes-bg-img{
    position: absolute;
    width: 50vw;
    object-fit: cover;
