    }
    .recipes-navbar-wrapper{
        flex-direction: row;
        padding: 10px 20px;
        justify-content: center;
    }
    .recipes-nav-logo,.recipes-back-icon-wrapper{
        display: none;
    }
    .recipes-navbar{
        width: 100%;
    }
    .recipes-nav-icon-wrapper{
        flex-direction: row;
        justify-content: space-evenly;
    }
    .recipes-img-container{
        grid-column: 1;
        grid-row: 2;
        width: 100vw;
        height: 30vh;
    }
    .recipes-title-wrapper{
        grid-column: 1;
        grid-row: 3;
        width: 100vw;
        height: 15vh;
        padding-left: 30px;
    }
    .rating-wrapper{
        grid-column: 1;
        grid-row: 3;
